.articleContent-shell {
    padding: 24px;
    width: 700px;

    .author_info_block {
        display: flex;
        align-items: center;
        margin-bottom: 20px;

        .author_info_box {
            flex-grow: 1;
            margin-left: 8px;

            .user_name {
                display: inline-block;
                max-width: 100%;
                font-size: 16;
                font-weight: 600;
                color: #333;
            }


        }

        .ant-btn {
            color: #6cbd45;
            border-color: #6cbd45;
        }
    }

}

.ant-layout-content {
    flex: none;

    .showMore {
        display: block;
        text-align: center;
    }
}

/*对 markdown 样式的补充*/
pre {
    display: block;
    padding: 10px;
    margin: 0 0 10px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #abb2bf;
    background: #282c34;
    word-break: break-all;
    word-wrap: break-word;
    overflow: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-top: 1em;
    /* margin-bottom: 1em; */
}

h1 {
    font-size: 1.5em;
}

h2 {
    font-size: 1.25em;
}

strong {
    font-weight: bold;
}

p>code:not([class]) {
    padding: 2px 4px;
    font-size: 90%;
    color: #c7254e;
    background-color: #f9f2f4;
    border-radius: 4px;
}

p img {
    /* 图片居中 */
    margin: 0 auto;
    display: flex;
}

#articleContent {
    font-family: "Microsoft YaHei", 'sans-serif';
    font-size: 16px;
    line-height: 30px;
}

#articleContent .desc ul,
#articleContent .desc ol {
    color: #333333;
    margin: 1.5em 0 0 25px;
}

#articleContent .desc h1,
#articleContent .desc h2 {
    border-bottom: 1px solid #eee;
    padding-bottom: 10px;
}

#articleContent .desc a {
    color: #009a61;
}

:-webkit-any(article, aside, nav, section) h1 {
    font-size: 1.5em;
}

:-webkit-any(article, aside, nav, section):-webkit-any(article, aside, nav, section) h1 {
    font-size: 1.17em;
}

.sub-comment {
    background-color: #fafbfc;
    padding: 10px;
}

.sub-comment-showmore {
    display: inline-block;
    width: 100%;
    background-color: #fafbfc;
    padding: 2px 20px 2px 2px;
    text-align: right;
}

.comment-action {
    padding-left: 8px;
    cursor: 'auto';
}

